'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';

function isActive(pathname: string, href: string) {
  if (href === '/') return pathname === '/';
  return pathname === href || pathname.startsWith(href + '/');
}

export default function Nav() {
  const pathname = usePathname();

  // Hide the header entirely on the Board page (clean TV view)
  if (pathname.startsWith('/board')) return null;

  const reportsActive = pathname.startsWith('/reports');

  return (
    <header className="site-header">
      <div className="wrap">
        <div className="brand">
          <img src="/mcafee-logo.png" alt="McAfee Custom Deer Processing" />
          <Link href="/">McAfee Deer Processing</Link>
        </div>

        <nav className="menu" aria-label="Primary">
          <Link href="/" className={`item ${isActive(pathname, '/') ? 'active' : ''}`}>
            Home
          </Link>

          <Link href="/intake" className={`item ${isActive(pathname, '/intake') ? 'active' : ''}`}>
            Intake form
          </Link>

          <Link href="/scan" className={`item ${isActive(pathname, '/scan') ? 'active' : ''}`}>
            Scan Tags
          </Link>

          <Link href="/search" className={`item ${isActive(pathname, '/search') ? 'active' : ''}`}>
            Search
          </Link>

          <Link href="/board" className={`item ${isActive(pathname, '/board') ? 'active' : ''}`}>
            Board
          </Link>

          {/* Reports dropdown */}
          <div
            className={`item dropdown ${reportsActive ? 'active' : ''}`}
            role="menu"
            tabIndex={0} // enables :focus-within for keyboard and touch
          >
            <span className="drop-label">
              Reports <span className="caret" aria-hidden>▾</span>
            </span>
            <div className="drop-menu">
              <Link
                href="/reports/calls"
                className={`drop-item ${isActive(pathname, '/reports/calls') ? 'active' : ''}`}
              >
                Call Report
              </Link>
              {/* add more reports here later */}
            </div>
          </div>
        </nav>
      </div>

      <style jsx>{`
        .site-header { border-bottom: 1px solid #e5e7eb; background: #fff; }
        .wrap { max-width: 1100px; margin: 0 auto; display: flex; align-items: center; justify-content: space-between; padding: 8px 12px; }
        .brand { display: flex; align-items: center; gap: 8px; font-weight: 800; }
        .brand img { height: 28px; }
        .menu { display: flex; gap: 12px; align-items: center; position: relative; }
        .item { padding: 6px 10px; border-radius: 8px; color: #334155; text-decoration: none; font-weight: 700; }
        .item.active, .item:hover { background: #eef2ff; color: #1e40af; }

        /* Dropdown */
        .dropdown { position: relative; cursor: default; }
        .drop-label { display: inline-flex; align-items: center; gap: 6px; user-select: none; }
        .caret { font-size: 10px; line-height: 1; }

        .drop-menu {
          position: absolute;
          top: 100%;          /* sit flush under trigger — NO GAP */
          left: 0;
          min-width: 180px;
          background: #fff;
          border: 1px solid #e5e7eb;
          border-radius: 8px;
          box-shadow: 0 6px 24px rgba(0,0,0,0.08);
          padding: 6px;
          display: none;
          z-index: 20;
        }

        /* Keep open while hovering OR while any child has focus (keyboard/touch) */
        .dropdown:hover .drop-menu,
        .dropdown:focus-within .drop-menu {
          display: block;
        }

        .drop-item {
          display: block;
          padding: 8px 10px;
          border-radius: 6px;
          color: #334155;
          text-decoration: none;
          font-weight: 700;
        }
        .drop-item:hover, .drop-item.active {
          background: #eef2ff;
          color: #1e40af;
        }

        @media (hover: none) and (pointer: coarse) {
          /* On touch devices, keep it visible while focused */
          .dropdown .drop-menu { display: ${reportsActive ? 'block' : 'none'}; }
        }
      `}</style>
    </header>
  );
}


